<script>
import NavigationBar from './NavigationBar.vue'

export default {
  name: 'HomePage',
  components: {
    NavigationBar
  },
  data: function() {
    return {
      options:[{
        name:'模板管理',
        img:require('../image/模板管理.png')
      },{
        name:'客户管理',
        img:require('../image/客户管理.png')
      },{
        name:'数据分析',
        img:require('../image/数据分析.png')
      }]
    }
  },
  methods: {
    optionHandler:function(name){
      switch(name){
        case '模板管理':
          this.$router.push('/main/templateList')
          break
        case '客户管理':
          this.$router.push('/main/guests')
          break
        case '数据分析':
          this.$router.push('/main/analysis')
          break
        default:
          break
      }
    }
  },
  mounted: function() {
    
  },
  beforeRouteEnter:function(to,from,next){
    next(vm => {
      if(vm.$store.state.role != 'ROLE_ADMIN1'){
        vm.$router.push('/403')
      }
    })
    // next()
  }
}
</script>

